	module.exports = {"data":{"markdownRemark":{"html":"<p><a href=\"/2933\">mew</a>には帰ってもらって、と。 <a href=\"/2933\">この前 HDD がクラッシュ</a>した。これしか HDD がナイので、何とか修復しようと試みる。……何とか認識は OK。でも領域が消えてるので再確保し、フォーマット。 もはや回りの人が見たらバカとしか思えない。でも、金のナイ私にとって使えなくなるよりマシ。と思い1時間後。  お、遅い……何だ、「アロケーションテーブル 4457 を修復中」って!!ま、いいや。ねる。 翌朝、まだフォーマット中。ざけんなこらぁ!! Ctrl + C や Esc が効かない。次の瞬間、危うくリセットするトコだった。気を落ち着けて朝食を取る。 で、部屋に戻ると <em>98％</em> っしゃぁ!!やっと終わった!!と喜び、とりあえず学校へ行き、帰宅後、FD より起動。 HDD にスキャンディスクをかける。だが、次の瞬間、見たものは… <strong>BBBBBBBBBBBBBBBBBBBBBBBBB…</strong> 唖然としたよ。こんなん見たの<a href=\"/pc-9821\">PC-9821 modelS1</a>以来だよ。しかも今回は FD じゃなく HDD…… さて、次は<a href=\"/2937\">再インストール編。乞うご期待!</a></p>","frontmatter":{"cover":null,"date":"1999-07-16T13:29:00+09:00","strDate":"1999/7/16","title":"やさいの国よりお達し"}}},"pathContext":{"path":"/ja/blog/1999/07/16-repairing/","langKey":"ja"}}

